.sweet-dessert {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.overlay-text {
  font-size: 2.25rem;
  margin: 2rem auto;
}
.card-div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 2rem;
  background-color: #fbf6ee;
  justify-content: center;
  justify-items: center;
  align-items: center;
  align-content: center;
  margin: 0 auto;
}
.card-color1 {
  background-color: #005e5c;
}
.card-color2 {
  background-color: #d0ba8e;
}
.card {
  padding: 2rem;
  border-radius: 1.563rem;
  color: #fff;
  box-shadow: 8px 18px 37px -15px rgb(0 0 0 / 16%);
  max-width: 70%;
}
.card hr {
  border-top: 0.5px;
  border-color: rgba(238, 238, 238, 0.27);
}
.card-flex {
  display: flex;
  align-content: center;
  align-items: center;
}
.card-heading {
  font-size: 1.5rem;
  margin: 0.75rem 0;
  padding: 0;
}
.card-text {
  font-size: 0.75rem;
  padding-bottom: 1rem;
  padding-right: 25%;
}
.card-price {
  font-size: 1.5rem;
}
@media screen and (max-width: 768px) {
  .menu-text {
    bottom: 7rem;
    font-size: 2.5rem;
  }
  .menu-under {
    bottom: 6rem;
  }
  .grid-img {
    max-width: 75%;
    margin: auto 1.5rem;
  }
  .sub-head {
    font-size: 1.5rem;
    margin: 1rem auto;
    font-weight: bold;
    margin-bottom: 0rem;
    margin-bottom: 0.5rem;
  }
  .overlay-text {
    font-weight: bold;
  }
  .grid1 {
    padding: 5rem 2rem;
  }
  .grid2 {
    padding: 5rem 2rem;
  }
  .grid3 {
    padding: 5rem 2rem;
  }
  .sub-header {
    font-size: 1rem;
    font-weight: bold;
  }
  .grid-price {
    font-size: 1rem;
    font-weight: bold;
  }
  .sub-text {
    font-size: 0.8rem;
    padding-right: 0;
  }
  .decoration {
    max-width: 45%;
    margin: auto;
    margin-bottom: 1.5rem;
  }
  .card {
    padding: 1.25rem;
    max-width: 80%;
  }

  .card-heading {
    font-size: 1.25rem;
    margin: 0.5rem 0;
  }

  .card-text {
    font-size: 0.6rem;
    padding-bottom: 1rem;
    padding-right: 8%;
    line-height: 0.85rem;
  }

  .card-price {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 425px) {
  .menu-under {
    bottom: 3rem;
    max-width: 22%;
  }
  .menu-text {
    bottom: 3.5rem;
    font-size: 1.625rem;
    left: 10%;
  }
  .sub-head {
    font-weight: bold;
    font-size: 1.8rem;
    margin-bottom: 0;
  }
  .decoration {
    margin-top: 0.5rem;
    max-width: 100%;
  }
  .sub-header {
    font-size: 1.25rem;
    font-weight: 600;
  }
  .grid-price {
    font-size: 1.25rem;
    font-weight: 600;
  }
  .sub-text {
    font-size: 0.875rem;
    padding-right: 0;
  }
  .grid {
    display: block;
  }
  .grid1 {
    padding: 5rem 2rem;
  }

  .grid2 {
    padding: 2rem;
  }
  .grid3 {
    padding: 2rem;
  }
  .grid-img {
    max-width: 90%;
    margin: 1rem;
  }
  .grid-img3 {
    margin: auto;
    max-width: 95%;
  }
  .btn {
    padding: 0.75rem 1.5rem;
  }
  .overlay-text {
    font-size: 1.7rem;
    font-weight: bold;
  }
  .card {
    padding: 0.75rem;
    max-width: 80%;
    margin: 1rem;
  }
  .card-div {
    display: block;
    grid-template-columns: none;
    padding: 2rem 1rem;
  }

  .card-heading {
    font-size: 1rem;
    margin: 0.5rem 0;
  }

  .card-text {
    font-size: 0.75rem;
    padding-bottom: 0.5rem;
  }

  .card-flex {
    display: block;
  }

  .card-price {
    font-size: 1rem;
    margin: 0.5rem 0;
  }
}