
.Heroimg{
    width: 100%;
   height: 100vh;
    padding-top: 3rem;
    padding-inline:1rem;
    display: grid;
    grid-template-columns: 20% 60% 20%;
    background: url('../../../public//images//Alain-Ducasse-scaled.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position:center;
    align-items: center;
    font-family:'Titillium Web', sans-serif;
    @media (max-width:600px) {
        display:block;
    }
    hr{
        @media (max-width:600px) {
            display: none; height: 400px;
        }
    }
    &-main-div{
        
         text-align: center;
         display: flex;
         flex-direction: column;
         color: white;
         height: 100%;
       
       
         &-1{
             width: 100%;
             height: 50%;
             display: flex;
    align-items: center;
    justify-content: center;
    
      h1{
             font-size:clamp(30px,5vw,70px);
              font-weight: 300;
        
         }
         }
         &-2{
             width: 100%;
             height: 100%;
             font-size: 16px;
             padding-inline: auto;
             display: flex;
             flex-direction: column;
           
             align-items: center;
             h2{
                 font-size: clamp(20px,2.5vw,50px);
                 width: 100%;
                 font-weight: 300;
                 text-align: center;
                 letter-spacing: 0.05em;
             }
             h3{
                 font-size: clamp(17px,2vw,30px);
                 color: rgb(5, 65, 5);
                 font-weight: 400;
             }
             
         }
    }
}